.field--name-field-agenda-items,
.field--name-field-referenced-docs,
.field--name-field-iasc-group-space,
.field--name-field-info-private {
  > .field__label {
    @extend .cd-heading-3;
  }

  .field__item {
    margin-bottom: 1rem;
  }
}

.field--name-field-ai-title {
  margin-bottom: 1rem;
  font-weight: bold;
}

.field--name-field-content .field--type-viewsreference {
  margin-bottom: 2rem !important;
}

.cd-location,
.field--name-field-contact,
.field--name-field-meeting-notes,
.field--name-field-meeting-agendas,
.field--name-field-oa-section,
.field--name-field-iasc-audience,
.field--name-field-category,
.field--name-field-links,
.field--name-field-organisation,
.field--name-field-membership,
.field--name-field-date-closed,
.field--name-field-external-link,
.field--name-group-content-access,
.field--name-field-ap-responsibility,
.field--name-field-referenced-docs,
.field--name-field-list-title,
.field--name-field-host {
  margin-bottom: 2rem !important;

  > .field__label,
  .cd-sidebar__label {
    @extend .cd-heading-2;
  }
}

.cd-layout-sidebar-first {
  .cd-location,
  .field--name-field-contact,
  .field--name-field-meeting-notes,
  .field--name-field-meeting-agendas,
  .field--name-field-oa-section,
  .field--name-field-iasc-audience,
  .field--name-field-category,
  .field--name-field-links,
  .field--name-field-organisation,
  .field--name-field-membership,
  .field--name-field-date-closed,
  .field--name-field-external-link,
  .field--name-group-content-access,
  .field--name-field-ap-responsibility,
  .field--name-field-referenced-docs,
  .field--name-field-list-title,
  .field--name-field-host {
    margin-bottom: 2rem !important;

    > .field__label,
    .cd-sidebar__label {
      @extend .cd-heading-3;
    }
  }

  // Margin resets because nested fields are hard.
  .node--view-mode-bio-long .field--name-field-organisation,
  .node--view-mode-bio-short .field--name-field-organisation {
    margin-bottom: 0 !important;
  }
}

.cd-layout-sidebar-first > .cd-sidebar,
.cd-layout-sidebar-first .field--name-field-organisation,
.cd-layout-sidebar-first .field--name-field-list-title  {
  margin-bottom: 1rem !important;
}

// Banner caption.
.field--name-field-banner-text {
  text-align: right;
  font-size: 0.9rem;
}

// This emulates the cd-bullet-list component using CSS bg image instead of SVG element in markup.
.field--name-field-links,
.field--name-field-referenced-docs,
.field--name-field-iasc-group-space {
  .field__item a {
    display: block;
    margin-bottom: 1rem;
    position: relative;
  }

  .field__item a::before {
    display: inline-block;
    height: 9px;
    width: 9px;
    margin-right: 1rem;
    content: '';
    background-image: url('data:image/svg+xml,\
    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 48 48">\
      <path fill="#ff4d59" d="M14,48a4,4,0,0,1-2.83-6.83L28.34,24,11.17,6.83a4,4,0,0,1,5.66-5.66l20,20a4,4,0,0,1,0,5.66l-20,20A4,4,0,0,1,14,48Z"/>\
    </svg>');
    background-repeat: no-repeat;
    background-position: 0 0;
  }
}

// This emulates the cd-bullet-list component without being able to output as a ul.
.field--name-field-meeting-notes > .field__items > .field__item,
.field--name-field-meeting-agendas > .field__items > .field__item {
  padding-bottom: 1rem;
}

.field--name-field-meeting-notes svg,
.field--name-field-meeting-agendas svg {
  fill: var(--iasc-highlight);
  width: 9px;
  height: 9px;
  display: inline-block;
  margin-right: 1rem;
}

// Multiple members separated by hr.
.field--name-field-membership > .field__items > .field__item + .field__item,
.field--name-field-contacts > .field__item + .field__item {
  margin-top: 1rem;
  padding-top: 1rem;
  border-top: 1px solid $cd-mid-bluey-grey;
}

.field__item p:first-child {
  margin-top: 0;
}
